'use client';

import { Container, Group, Burger, Title, Text, Button } from '@mantine/core';
import { useDisclosure } from '@mantine/hooks';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { ThemeToggle } from './ThemeToggle';
// Header.module.css removed, using inline styles

const links = [
    { link: '/schedule', label: 'Schedule' },
    { link: '/facilities', label: 'Facilities' },
    { link: '/guide', label: 'Guide' }, // Added Guide link
];

export function Header() {
    const [opened, { toggle }] = useDisclosure(false);
    const pathname = usePathname();

    return (
        <header style={{
            position: 'sticky',
            top: 0,
            zIndex: 100,
            backgroundColor: 'var(--mantine-color-body)',
            borderBottom: '1px solid var(--mantine-color-default-border)',
            backdropFilter: 'blur(20px)',
            background: 'rgba(255, 255, 255, 0.8)', // Fallback for light mode, will need adjustment for dark
        }}>
            <Container size="lg" h={60}>
                <Group justify="space-between" h="100%">
                    <Link href="/" style={{ textDecoration: 'none', color: 'inherit' }}>
                        <Group gap="xs">
                            {/* Logo placeholder */}
                            <Text fw={900} size="lg" variant="gradient" gradient={{ from: 'blue', to: 'cyan' }}>NH Casting</Text>
                        </Group>
                    </Link>

                    <Group gap={5} visibleFrom="xs">
                        {links.map((link) => (
                            <Button
                                key={link.label}
                                component={Link}
                                href={link.link}
                                variant={pathname === link.link ? 'light' : 'subtle'}
                                color="gray"
                                radius="md"
                            >
                                {link.label}
                            </Button>
                        ))}
                        <ThemeToggle />
                    </Group>

                    <Burger opened={opened} onClick={toggle} hiddenFrom="xs" size="sm" />
                </Group>
            </Container>
        </header>
    );
}
